html,
body,
#root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  font-size: 62.5%;
}

p,
a,
div {
  font-size: 16px;
}

a {
  color: #074166;
  text-decoration: none;
}



ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

ul li {
  padding: 0;
  margin: 0;
}

input[type="text"] {
  box-sizing: border-box;
  border: 1px solid hsl(0, 0%, 80%);
  border-radius: 4px;
  width: 100%;
  padding: 7px 10px;
}

/* Temporary fix to overwrite and display the default style of the checkbox
to prevent issues with the custom component and react/fontawesome missing icons */

input[type="checkbox"] {
  appearance: checkbox;
  -webkit-appearance: checkbox;
  -moz-appearance: checkbox;
  margin: 0 0 0 16px;
}

input[type="checkbox"]::before {
  content: none;
}

/**
Ends temporary react/fontawesome fix
*/

.link,
.link-button {
  color: #074166;
  font-family: "Roboto", cursive;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.75px;
  line-height: 14px;
  text-align: center;
  text-decoration: none;
  padding: 0;
}

.link:active,
.link-button:active,
.link:hover,
.link-button:hover {
  text-decoration: none;
}

* {
  font-family: sans-serif, "Roboto", "Segoe UI";
}

.form-group:first-child {
  margin-top: 0;
}

.form-group > label {
  color: #074166;
  font-family: "Roboto";
  font-size: 11px;
  font-weight: bold;
  letter-spacing: 0.75px;
  line-height: 14px;
  text-transform: uppercase;
  display: block;
}

.form-group > input {
  height: 38px;
  width: 100%;
  margin: 10px 0;
  border-radius: 4px;
  border: solid 1px #ccc;
  box-sizing: border-box;
}

form-group .select-role {
  margin: 10px 0;
}

/* Overriding the center-align property from the styleguide */
.solid-provider-select {
  text-align: left;
}

.solid-provider-select
.option {
  padding-left: 10px;
}

.nav.nav > ul {
  padding: 20px;
  height: 60px;
}

.nav.nav__toolbar > ul > li > a,
.nav.nav__toolbar > ul > li > button {
  color: #074166;
}

.nav.nav > ul > li > a:hover,
.nav.nav > ul > li > a.active,
.nav.nav > ul > li > button:hover,
.nav.nav > ul > li > button.active {
  color: #074166;
  background: rgba(7, 65, 102, 0.3);
}

.nav.nav__toolbar > ul > li {
  text-align: center;
  line-height: 60px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  border-left: 1px solid rgba(8, 53, 117, 0.1);
}

/* boton notificaciones */
.fUwLnT:active,
.fUwLnT:focus,
.fUwLnT:hover,
.fUwLnT
.active { 
  background: rgba(7, 65, 102, 0.3);
  outline: none;
}

/* menu dropdown sobre el icono perfil */
.jOLVKi button:hover {
  background: rgba(7, 65, 102, 0.3);
  transition: all 0.5s ease-in-out;
  color: #083575;
}
